body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background-color: #f4f6f8;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }
  
  .post-card {
    transition: transform 0.3s;
  }
  
  .post-card:hover {
    transform: scale(1.05);
  }
  
  .search-bar {
    margin-bottom: 20px;
  }
  